.container {
    background-color: black; /* Change the background to black */
    color: white; /* Change the font color to white */
    text-align: center;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.back-button {
    position: absolute;
    top: 20px;
    left: 20px;
    background-color: #333; /* Style for the back button */
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    z-index: 10;
}

.text-container {
    width: 100%;
    height: 80%;
    display: block;
    margin: 0 auto;
}

.slider-container {
    width: 40%; /* Set the slider container to 40% of the page */
    margin: 20px 0;
}

.slider {
    width: 100%;
    cursor: pointer;
}

.labels-container {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.slider-label {
    font-size: 0.8rem;
    color: white;
}

.survey-link {
    background-color: #444; /* Style for the survey link */
    color: white;
    padding: 10px 15px;
    margin-top: 20px;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease;
}

.survey-link:hover {
    background-color: #555; /* Hover effect */
}
